
import { ref, reactive, getCurrentInstance, onMounted } from "vue";
export default {
  name: "CategoryList",
  setup() {
     // @ts-ignore
    const { proxy } = getCurrentInstance();
    let items = reactive({ todo: [] });

     // 请求
    let reqs = async () => {
      let res = await proxy.$http.get(
        "rest/items"
      );
      console.log(res);
      items.todo = res.data;
      console.log("items", items);
    };
    

    // 删除
    let remove = (row:any) => {
      proxy.$confirm(`是否确定要删除分类 "${row.name}"`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(async () => {
        const res = await proxy.$http.delete(`rest/items/${row._id}`);
        proxy.$message({
          type: "success",
          message: "删除成功!",
        });
       reqs();
      });
    };

   onMounted(()=>{
     reqs()
   })

    return {
      items,
      remove,
    };
  },
};
